
import { defineComponent, ref, onMounted, toRefs } from "vue";
import SearchBar from "@/components/SearchBar.vue";
import { iCreateDialog } from "@/typings/component";
import { getMembers } from "@/apis/member";
import { Member } from "@/typings/member";
import { formatImageUrl, loading } from "@/utils";
import DetailTable from "./DetailTable.vue";
import { getCompany, postCompanyLogo, putCompanyLogo } from "@/apis/company";
import { CompanyResponse } from "@/typings/company";

const Component = defineComponent({
  name: "Colleagues",
  components: {
    SearchBar,
    DetailTable
  },
  props: {
    id: {
      type: String,
      default: "0"
    }
  },
  setup(props) {
    const { id } = toRefs(props);
    const colleaguesAddDialog = ref<iCreateDialog | null>(null);
    const searchValue = ref("");
    const detail = ref<CompanyResponse | null>(null);
    const list = ref<Member[]>([]);
    const timeId = ref<ReturnType<typeof setTimeout> | undefined>(undefined);


    const init = async () => {
      const loadingInstance = loading();
      try {
        const { data: members } = await getMembers({
          company: parseInt(id.value, 10)
        });
        const { data: company } = await getCompany(parseInt(id.value, 10));
        list.value = members;
        detail.value = company;
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onAddColleagueClick = () => {
      if (!colleaguesAddDialog.value) return;
      colleaguesAddDialog.value.open();
    };

    const onSearchChange = async (value: string) => {
      searchValue.value = value;

      timeId.value && clearTimeout(timeId.value);
      timeId.value = setTimeout(async () => {
        const { data } = await getMembers({
          company: parseInt(id.value, 10),
          q: searchValue.value
        });
        list.value = data;
      }, 500);
    };

    const onLoad = async () => {
      await init();
    };

    const onUploadLogo = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.multiple = false;
      element.addEventListener("change", async () => {
        if (element.files) {
          const { data } = await postCompanyLogo(element.files[0]);
          await putCompanyLogo(parseInt(id.value, 10), data.id);
          await init();
        }
      });
      element.click();
    };

    return {
      list,
      detail,
      colleaguesAddDialog,
      searchValue,

      onAddColleagueClick,
      onSearchChange,
      onLoad,
      onUploadLogo,

      formatImageUrl
    };
  }
});

export default Component;
